import React from 'react';
import * as styles from './cta-button.module.scss';

interface CtaProps {
    call: string;
    buttonLabel: string;
    action: () => void;
}

const CtaButton = ({call = '', buttonLabel = '', action = () => {/*test*/}}: CtaProps) => {
    return (
        <div className={styles.cta}>
            <div className={styles.ctaCall}>{call}</div>
            <button className={styles.ctaButton} onClick={action}>{buttonLabel}</button>
        </div>
    );
};

export default CtaButton;
